body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 32px 0 16px;
  display: flex;
}

.product-card__logo {
  margin-bottom: 40px;
  display: flex;
}

.product-card__logo-img {
  width: 160px;
  height: 134px;
}

.product-card__info {
  width: 166px;
  margin-bottom: 16px;
  padding: 0;
  font-family: Roboto, sans-serif;
  line-height: 18px;
}

.product-card__info-title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
}

.product-card__info-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.product-card__info-review {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product-card__info-review-stars {
  justify-content: space-between;
  align-items: center;
  width: 96px;
  display: flex;
}

.product-card__info-review-stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.product-card__info-review-stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.product-card__info-review-count {
  align-items: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__info-price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__info-price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.product-card__info-price-sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.product-card__button {
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  display: flex;
}

.product-card__button-link {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.product-card__button:hover {
  background-color: #fff;
}

.product-card__button:hover > .product-card__button-link {
  color: #00acdc;
}
